body {
  font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif; 
}

.written-summary {
  border: solid 1px green;
}

.written-summary:invalid,
.written-summary:invalid:focus {
  border: dashed 1px #ec5b62;
}

.written-summary:invalid:focus {
  background: rgba(236, 91, 98, 0.1);
}

.example-warper {
  padding: 2px 5%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  border: 1px #cfcece; // dashed;
  margin: 5px;
  white-space: pre-line;
  color: #000;
  .button {
    font-family: Arial, Helvetica, sans-serif; //"Roboto", Arial, sans-serif;
    color: #fff;
    cursor: pointer;
    padding: 0px 30px;
    display: inline-block;
    margin: 1px 15px;
    text-transform: uppercase;
    line-height: 2em;
    letter-spacing: 1.5px;
    font-size: 2em;
    outline: none;
    position: relative;
    font-size: 15px;
    background-color: #2680eb;
    border-radius: 15px 15px 15px 15px;
    border: none;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  .button:hover,
  .button.hover {
    background-color: #095aba;
  }
  /*
  card
  */
  .card {
    font-size: 12px;
  }
  .card > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 14px;
    text-align: center;
  }

  .modal h2 {
    color: #333;
    font-weight: 400;
    border-bottom: solid 1px black;
    margin: 0 0 10px 0;
    padding-bottom: 10px;
  }

  .modal .content {
    width: 60%;
    margin: 0 auto;
    padding: 20px;
    line-height: 1.5em;
  }

  .modal dt { font-weight: bold; }
  .modal dd { margin: 0 0 1em 0; }

  .modal dl li {
    margin: 0.5em 0;    
  }

  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }

  .example-warper-start {
    width: 100%;
    padding: 20px 5%;
    display: flex;
    flex-wrap: wrap;
    border: 1px #cfcece dashed;
  }

  .menu {
    width: 200px;
    display: flex;
    flex-direction: column;
    background: #ffffff;
  }
  .menu-item {
    cursor: pointer;
    padding: 5px;
    height: 28px;
    border-top: 1px solid rgb(187, 187, 187);
  }
  .menu-item:hover {
    color: #2980b9;
  }
}
